import React, {Component} from 'react';
import {Col, FormGroup, Label, Modal, ModalBody, ModalHeader, Row} from 'reactstrap';
import 'react-table/react-table.css'
import {requestK} from "../../../helper/APIUtils";
import moment from "moment/moment";


class PasswordDetails extends Component {

  constructor(props) {
    super(props);
    this.state = {
      displayed: false,
      content: [],
      loading: true
    };
    this.getPasswordHistory = this.getPasswordHistory.bind(this);
  }

  componentDidMount() {
    this.getPasswordHistory(this.props.employee);
  }

  getPasswordHistory(employee) {
    let self = this;
    let params = {
      url: `/passwordCreation/passwordHistory`,
      method: "post",
      data: employee.employeeUserId
    };
    self.setState({loading: true});
    requestK(params)
      .then(function (response) {
        self.setState({
          content: response.data ? response.data : [],
          loading: false
        });
      }).catch(function (error) {
      self.setState({loading: false});
    });
  }

  render() {
    let employee = this.props.employee;

    return (
      <Modal isOpen={this.props.modal} size="sm" toggle={e => this.props.toggle(e)}>
        <ModalHeader className="justify-content-center" toggle={e => this.props.toggle(e)}>
          <label style={{paddingRight: '2em', fontSize: '17px'}}>{(employee ? (employee.name + " " + employee.surname + " için ") : "") + "Şifre Değişim Detayları"}</label>
        </ModalHeader>
        <ModalBody>
          {this.state.content.length === 0 &&
          <Row style={{padding: '0px 0px 0px 0px', margin: '10px 15px 0px 0px', marginLeft: '20px'}}>
            <Col sm="12">
              <FormGroup row>
                <Label sm={12} style={{fontWeight: 'bold'}}>Henüz şifre değişikliği yapılmamıştır.</Label>
              </FormGroup>
            </Col>
          </Row>}
          {(this.state.loading === true || (this.state.content && this.state.content.length > 0)) &&
          <Row style={{padding: '0px 0px 0px 0px', margin: '10px 15px 0px 0px', marginLeft: '20px'}}>
            <Col sm="12">
              <FormGroup row>
                <Label sm={12} style={{fontWeight: 'bold'}}>Son 5 Şifre Değişim Tarihi</Label>
              </FormGroup>
            </Col>
            {this.state.content.length > 0 &&
            <Col sm="12">
              <FormGroup row>
                <Label sm={4} style={{fontWeight: 'bold'}}>Tarih: </Label>
                <Col sm={8}>
                  <label className="form-control-label">
                    {moment(this.state.content[0].changedAt.toString().replace("T", " ")).format("DD-MM-YYYY HH:mm:ss")}
                  </label>
                </Col>
              </FormGroup>
            </Col>}
            {this.state.content.length > 1 &&
            <Col sm="12">
              <FormGroup row>
                <Label sm={4} style={{fontWeight: 'bold'}}>Tarih: </Label>
                <Col sm={8}>
                  <label className="form-control-label">
                    {moment(this.state.content[1].changedAt.toString().replace("T", " ")).format("DD-MM-YYYY HH:mm:ss")}
                  </label>
                </Col>
              </FormGroup>
            </Col>}
            {this.state.content.length > 2 &&
            <Col sm="12">
              <FormGroup row>
                <Label sm={4} style={{fontWeight: 'bold'}}>Tarih: </Label>
                <Col sm={8}>
                  <label className="form-control-label">
                    {moment(this.state.content[2].changedAt.toString().replace("T", " ")).format("DD-MM-YYYY HH:mm:ss")}
                  </label>
                </Col>
              </FormGroup>
            </Col>}
            {this.state.content.length > 3 &&
            <Col sm="12">
              <FormGroup row>
                <Label sm={4} style={{fontWeight: 'bold'}}>Tarih: </Label>
                <Col sm={8}>
                  <label className="form-control-label">
                    {moment(this.state.content[3].changedAt.toString().replace("T", " ")).format("DD-MM-YYYY HH:mm:ss")}
                  </label>
                </Col>
              </FormGroup>
            </Col>}
            {this.state.content.length > 4 &&
            <Col sm="12">
              <FormGroup row>
                <Label sm={4} style={{fontWeight: 'bold'}}>Tarih: </Label>
                <Col sm={8}>
                  <label className="form-control-label">
                    {moment(this.state.content[4].changedAt.toString().replace("T", " ")).format("DD-MM-YYYY HH:mm:ss")}
                  </label>
                </Col>
              </FormGroup>
            </Col>}
          </Row>}
        </ModalBody>
      </Modal>
    );
  }
}

export default PasswordDetails;