import React, {Component} from "react";
import Table from "../../../../components/Table/Table";
import MiniButton from "../../../../components/MiniButton";
import Swal from "sweetalert2";
import {renewListForPrivileges} from "../../common/PeykHelper";
import DetailedSearch from "../../../../components/DetailedSearch/DetailedSearch";
import {requestK} from "../../../../helper/APIUtils";
import Alert from "react-s-alert";

class PasswordBlockedAdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      alert: null,
      loading: false,
      displayDetailDialog: false,
      manual: false,
      list: [],
      total: 0,
      searching: false,
      searchQuery: undefined,
      page: 0,
      size: 10,
      selected: undefined,
      selection: this.props.selection,
      selectedAll: false,
      showOnlyNoFirstAttempt: false,
      locale: {
        back: 'Geri',
        close: 'Kapat',
        last: 'Son',
        next: 'İleri',
        skip: 'Atla',
      },
      joyrideActive: false,
      shouldRun: true,
      width: window.innerWidth,
      flag: 0,
      showEczaciHelpModal: false,
      video: null,
      stop: false,
      hasAutoRegistry: true,
      activeTab: '0',
      showModal: false
    };
    this.fetchData = this.fetchData.bind(this);
    this.handleSearch = this.handleSearch.bind(this);
    this.getTrStyles = this.getTrStyles.bind(this);
    this.isSelected = this.isSelected.bind(this);
    this.unblock = this.unblock.bind(this);
  }

  componentDidMount() {
  }

  isSelected(id) {
    return (this.state.selection.indexOf(id) !== -1);
  }

  getTrStyles(item) {
    return (item.statusType && item.statusType !== 'ACTIVE') ? {
      backgroundColor: 'lightgray',
      webkitTextFillColor: 'darkgray'
    } : {};
  }

  handleSearch(searchQuery) {
    this.setState({
      searchQuery,
      searching: true,
      // selection: [],
    }, () => this.fetchData(this.state.accessor, this.state.direction, 0, this.state.size))
  }

  static alertSuccessReset() {
    Alert.success("IK Admin hesabının blokesi kaldırılmıştır.", {
      position: 'top-right',
      effect: 'stackslide',
      timeout: 5000
    });
  }

  static alertWarningReset() {
    Alert.error("IK Admin hesabında bloke kaldırılamadı. Lütfen sistem sağlayıcınız ile görüşünüz.", {
      position: 'top-right',
      effect: 'stackslide',
      timeout: 5000
    });
  }

  toggleCheckboxChange(e, row, id) {
    let self = this;
    let checked = e.target.checked;
    let items = self.state.list;
    let selection = self.state.selection;
    if (row === "all") {
      let selection = [];
      if (checked) {
        items.forEach(item => {
          selection.push(item[id]);
        });
      }
      self.setState({selection: selection, selectedAll: checked});
      self.props.updateSelection(selection);
    } else {
      let index = selection.indexOf(row[id]);
      if (index >= 0) {
        selection = [
          ...selection.slice(0, index),
          ...selection.slice(index + 1)
        ];
      } else {
        selection.push(row[id]);
      }
      self.setState({
        selection: selection,
        selectedAll: ((selection.length > 0) ? (selection.length === self.state.list.length) : false)
      });
      self.props.updateSelection(selection);
    }
  }

  unblock(userId) {
    this.setState({alert: null});
    let self = this;
    if (userId) {
      let params = {
        url: "/passwordCreation/resetIkAdminBlock",
        method: "post",
        data: userId
      };
      self.setState({disableBtn: true});
      requestK(params)
        .then(function (response) {
          self.setState({disableBtn: false});
          self.props.refresh();
          return PasswordBlockedAdminPage.alertSuccessReset();
        }).catch(function (error) {
        self.setState({disableBtn: false});
        return PasswordBlockedAdminPage.alertWarningReset();
      });
    } else {
      Alert.error("Şu an işleminiz gerçekleştirilemiyor. Lütfen daha sonra tekrar deneyiniz.", {
        position: 'top-right',
        effect: 'stackslide',
        timeout: 5000
      });
    }
  }

  fetchData(accessor, direction, page, size) {
    let self = this;
    let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '&sort=recordId,desc';
    let searchQuery = !self.state.searchQuery ? '' : '&' + self.state.searchQuery;
    let params = {
      url: '/employee/readAdminPasswordBlockedList?page=' + page + '&size=' + size + searchQuery + sortStr,
      method: "post",
    };
    self.setState({loading: true, searching: true, manual: self.manual === true ? false : self.manual});
    self.props.refreshCompleted();
    requestK(params)
      .then(function (response) {
        if (!!response.data) {
          self.props.show();
        }
        self.setState({
          page: page,
          size: size,
          total: response.data ? response.data.totalElements : 0,
          list: response.data ? response.data.content : [],
          loading: false,
          searching: false
        });
      })
      .catch(function (error) {
        self.setState({loading: false, searching: false});
      });
  }

  renderMainTable() {
    let pageIdList = [];
    if (this.state.list && this.state.list instanceof Array) {
      this.state.list.map(e => {
        pageIdList.push(e.id);
      });
    }

    let columnFieldList = [
      {
        Header: "Ad",
        accessor: "firstName"
      },
      {
        Header: "Soyad",
        accessor: "lastName"
      },
      {
        Header: "Kullanıcı Adı",
        accessor: "username"
      },
      {
        Header: "Giriş Blokesi",
        accessor: "loginBlock",
        Cell: row => (
          <div>
            <label className="fas fa-lock" style={{color: '#d05d37', fontSize: '1.2em', cursor: 'pointer'}}/>
          </div>),
        sortable: false,
        width: '5%'
      },
      {
        Header: "e-Posta Adresi",
        accessor: "email",
        Cell: row => (<div>
          <div style={{margin: 0}}>
            {row.email}
          </div>
        </div>),
        width: "250em"
      },
      {
        Header: "İşlemler",
        accessor: "",
        sortable: false,
        Cell: row => {
          return (
            <div style={{textAlign: 'left', WebkitTextFillColor: 'white'}}>
              {this.state.list && !(row.statusType && row.statusType === 'TERMINATED') &&
              <MiniButton title="Bloke Kaldır" faIcon="fas fa-check-square"
                          disabled={this.state.disableBtn}
                          onClick={() => {
                            Swal({
                              title: 'UYARI',
                              text: ("Kullanıcı giriş blokesi kaldırılacak. Onaylıyor musunuz?"),
                              icon: 'warning',
                              showCancelButton: true,
                              reverseButtons: true,
                              confirmButtonText: 'Tamam',
                              confirmButtonColor: '#d33',
                              cancelButtonText: 'Vazgeç'
                            }).then((result) => {
                              if (result.value) {
                                this.unblock(row.recordId)
                              } else if (result.value === 0) {
                                this.setState({alert: null});
                              }
                            })
                          }}
                          color="text-success"/>}
            </div>
          )
        },
        width: 'auto'
      }
    ];
    let columns = renewListForPrivileges(this, columnFieldList, "accessor", this.props.isKepOwner);

    return <Table columns={columns}
                  content={this.state.list}
                  loading={this.state.loading}
                  total={this.state.total}
                  page={this.state.page}
                  size={this.state.size}
                  manual={this.state.manual || this.props.manual}
                  options={["5", "10", "20", "50", "100"]}
                  getTrStyles={this.getTrStyles}
                  fetchData={this.fetchData}/>
  }

  render() {
    let steps = this.state.steps;
    let locale = this.state.locale;

    const fieldList = [
      {
        label: "İsim",
        accessor: "firstName",
        type: "text",
        operator: "=",
      },
      {
        label: "Soyisim",
        accessor: "lastName",
        type: "text",
        operator: "=",
      },
      {
        label: "e-Posta Adresi",
        accessor: "email",
        type: "text",
        operator: "=",
      }
    ];
    let fields = renewListForPrivileges(this, fieldList, "accessor", this.props.isKepOwner);

    return <div className="row">
        <div className="card-body">
          <DetailedSearch fields={fields}
                          loading={this.state.searching}
                          onSearch={this.handleSearch}/>
        </div>

        <div className="card-body">
          <div className="row" style={{cursor: 'pointer', marginLeft: '10px', color: 'red'}}>
          <span>
            {(this.props.selection.length !== 0 ? "Seçilen hesap sayısı " + this.props.selection.length : "")}
          </span>
          </div>
          {this.renderMainTable()}
        </div>
        {this.state.alert}
      </div>
  }

}

export default PasswordBlockedAdminPage;