import React, {Component} from 'react';
import 'react-table/react-table.css';
import {requestK} from "../../../../helper/APIUtils";
import {showAxiosError} from "../../../../helper/helper";
import Alert from "react-s-alert";
import SweetAlert from "react-bootstrap-sweetalert";
import DropdownCheck from "../../../../components/Table/DropdownCheck";
import TextOverFlowTooltip from "../../../../components/TextOverFlowTooltip";
import MiniButton from "../../../../components/MiniButton";
import 'moment/locale/tr';
import moment from "moment";
import Swal from "sweetalert2";
import {
    departmentData,
    passwordCreationStatus,
    statusTypeFilterData,
    statusTypeFilterDataPassword
} from "../../common/PeykHelper";
import Table from "../../../../components/Table/Table";
import DetailedSearch from "../../../../components/DetailedSearch/DetailedSearch";
import PasswordDetails from "../PasswordDetails";
import DisplaySelectionList from "../../../../helper/DisplaySelectionList";


class NewPasswordRequest extends Component {
    constructor(props) {
        super(props);
        this.state = {
            alert: null,
            loading: false,
            displayDetailDialog: false,
            manual: false,
            list: [],
            total: 0,
            searching: false,
            searchQuery: undefined,
            page: 0,
            size: 10,
            selected: undefined,
            selection: this.props.selection,
            selectedAll: false,
            showOnlyNoFirstAttempt: false,
            steps: [
                {
                    target: '.baseBackground',
                    content: "Calisan sifre yonetimi",
                    placement: "center",
                },
                {
                    target: '.zocial',
                    content: 'Bordro yüklemek için lütfen "Ücret Bordrosu Yükle" butonuna tıklayınız.',
                    placement: "bottom-start",
                },
            ],
            locale: {
                back: 'Geri',
                close: 'Kapat',
                last: 'Son',
                next: 'İleri',
                skip: 'Atla',
            },
            joyrideActive: false,
            shouldRun: true,
            width: window.innerWidth,
            flag: 0,
            showEczaciHelpModal: false,
            video: null,
            stop: false,
            hasAutoRegistry: true,
            displaySelectionList: false,
            departmentList: [],
        };

        this.fetchData = this.fetchData.bind(this);
        this.handleSearch = this.handleSearch.bind(this);
        this.getTrStyles = this.getTrStyles.bind(this);
        this.openDetailDialog = this.openDetailDialog.bind(this);
        this.isSelected = this.isSelected.bind(this);
        this.closeDetailDialog = this.closeDetailDialog.bind(this);
        this.passwordSetReset = this.passwordSetReset.bind(this);
        this.passwordReset = this.passwordReset.bind(this);
        this.sendHomePageLink = this.sendHomePageLink.bind(this);
        this.callback = this.callback.bind(this);
    }

    componentDidMount() {
        this.checkAutoRegistry();
        this.readDepartmentAll();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props && this.props.updateTable) {
            this.fetchData(this.state.accessor, this.state.direction, this.state.page, this.state.size);
            this.props.changeUpdateTable();
        }
    }

    fetchData(accessor, direction, page, size) {
        let self = this;
        let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '&sort=id,desc';
        let searchQuery = !self.state.searchQuery ? '' : '&' + self.state.searchQuery;
        let params = {
            url: '/employee/readEmployeePasswordList?page=' + page + '&size=' + size + searchQuery + sortStr +
                ((searchQuery !== '' && searchQuery.includes("showOnlyNoFirstAttempt")) ? '' : '&showOnlyNoFirstAttempt=') +
                '&lastRecord=',
            method: "post",
        };
        self.setState({loading: true, searching: true, manual: self.manual === true ? false : self.manual});
        self.props.refreshCompleted();
        requestK(params)
            .then(function (response) {
                self.setState({
                    page: page,
                    size: size,
                    total: (response.data && response.data.page) ? response.data.page.totalElements : 0,
                    list: (response.data && response.data.page) ? response.data.page.content : [],
                    totalIdList: response.data ? response.data.selectedIdList : [],
                    loading: false,
                    searching: false
                });
            })
            .catch(function (error) {
                self.setState({loading: false, searching: false});
                showAxiosError(error);
            });
    }

    readDepartmentAll = () => {
        let self = this;
        let params = {
            url: '/department/readDepartment',
            method: "get",
        };
        requestK(params)
            .then(function (response) {
                let departmentList = response.data;
                self.setState({departmentList});
            }).catch(function (error) {
            showAxiosError(error);
        });
    };

    handleSearch(searchQuery) {
        this.setState({
            searchQuery,
            searching: true,
            // selection: [],
        }, () => this.fetchData(this.state.accessor, this.state.direction, 0, this.state.size))
    }

    openDetailDialog(row) {
        let clone = Object.assign({}, row);
        delete clone["_$visited"];
        this.setState({
            selected: clone,
            displayDetailDialog: true
        });
    }

    closeDetailDialog() {
        this.setState({
            selected: undefined,
            displayDetailDialog: false
        });
    }

    callback(data) {
        if (data.action === 'close' && data.type === 'step:after') {
            this.setState({shouldRun: false});
            this.setState({flag: 0});
        }
        if (data.action === 'stop' || data.action === 'reset' || data.action === 'skip') {
            this.setState({shouldRun: true});
            this.setState({joyrideActive: false});
            this.setState({flag: 0});
            this.activateJoyride();
        }
    }

    toggleCheckboxChange(e, row, id) {
        let self = this;
        let checked = e.target.checked;
        let items = self.state.list;
        let selection = self.state.selection;
        if (row === "all") {
            let selection = [];
            if (checked) {
                items.forEach(item => {
                    selection.push(item[id]);
                });
            }
            self.setState({selection: selection, selectedAll: checked});
            self.props.updateSelection(selection);
        } else {
            let index = selection.indexOf(row[id]);
            if (index >= 0) {
                selection = [
                    ...selection.slice(0, index),
                    ...selection.slice(index + 1)
                ];
            } else {
                selection.push(row[id]);
            }
            self.setState({
                selection: selection,
                selectedAll: ((selection.length > 0) ? (selection.length === self.state.list.length) : false)
            });
            self.props.updateSelection(selection);
        }
    }

    isSelected(id) {
        return (this.state.selection.indexOf(id) !== -1);
    }

    getTrStyles(item) {
        return (item.statusType && item.statusType !== 'ACTIVE') ? {
            backgroundColor: 'lightgray',
            webkitTextFillColor: 'darkgray'
        } : {};
    }

    passwordSetReset(userId) {
        if (userId) {
            let params = {
                url: "/passwordCreation/resetPasswordSet",
                method: "post",
                data: userId
            };
            requestK(params)
                .then(function (response) {
                })
                .catch(function (error) {
                });
        }
    }

    passwordReset(userId) {
        this.setState({alert: null});
        let self = this;
        if (userId) {
            let params = {
                url: "/user/resetPassword",
                method: "post",
                data: userId
            };
            self.setState({disableBtn: true});
            requestK(params)
                .then(function () {
                    self.passwordSetReset(userId);
                    self.sendLink(userId, 1, 1);
                    self.setState({disableBtn: false});
                    return true;
                }).catch(function () {
                self.setState({disableBtn: false});
                return NewPasswordRequest.alertWarning(1, 1);
            });
        } else {
            Alert.error("Çalışanın kullanıcı hesabı henüz oluşmamıştır. Lütfen daha sonra tekrar deneyiniz.", {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 5000
            });
        }
    }

    checkAutoRegistry = () => {
        let self = this;
        let params = {
            url: '/companyinfo/readEmployeeRegistryNotification',
            method: "get"
        };
        requestK(params)
            .then(function (response) {
                self.setState({hasAutoRegistry: response.data});
            }).catch(function (error) {
            showAxiosError(error);
        });
    };


    sendLink = (userId, actual, count) => {
        let self = this;
        if (userId) {
            let params = {
                url: "/passwordCreation/managerReset",
                method: "post",
                data: userId
            };
            self.setState({disableBtn: true});
            requestK(params)
                .then(function (response) {
                    self.setState({disableBtn: false});
                    if (response.data !== "ERROR") {
                        self.setState({manual: true});
                        return NewPasswordRequest.alertSuccess(actual, count);
                    } else {
                        return NewPasswordRequest.alertWarning(actual, count);
                    }
                }).catch(function () {
                self.setState({disableBtn: false});
                return NewPasswordRequest.alertWarning(actual, count);
            });
        } else {
            Alert.error("Çalışanın kullanıcı hesabı henüz oluşmamıştır. Lütfen daha sonra tekrar deneyiniz.", {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 5000
            });
        }
    };


    sendHomePageLink(userId, actual, count) {
        this.setState({alert: null});
        if (userId) {
            let self = this;
            let params = {
                url: "/passwordCreation/sendHomePageLink",
                method: "post",
                data: userId
            };
            self.setState({disableBtn: true});
            requestK(params)
                .then(function () {
                    self.setState({disableBtn: false});
                    return NewPasswordRequest.alertSuccess(actual, count);
                }).catch(function () {
                self.setState({disableBtn: false});
                return NewPasswordRequest.alertWarning(actual, count);
            });
        } else {
            return NewPasswordRequest.alertWarning(actual, count);
        }
    }

    static alertSuccess(actual, count) {
        if (actual >= count) {
            Alert.success("Link başarıyla gönderildi.", {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 5000
            });
        }
    }

    static alertWarning(actual, count) {
        if (actual >= count) {
            Alert.error("Link gönderilemedi, lütfen daha sonra tekrar deneyiniz!", {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 5000
            });
        }
    }

    static alertSuccessReset() {
        Alert.success("Çalışan hesabının blokesi kaldırılmıştır.", {
            position: 'top-right',
            effect: 'stackslide',
            timeout: 5000
        });
    }

    static alertWarningReset() {
        Alert.error("Çalışanın hesabında bloke kaldırılamadı. Lütfen sistem sağlayıcınız ile görüşünüz.", {
            position: 'top-right',
            effect: 'stackslide',
            timeout: 5000
        });
    }


    trigerModal = () => {
        this.setState({displaySelectionList: true});
    };

    renderMainTable() {
        let pageIdList = [];
        if (this.state.list && this.state.list instanceof Array) {
            this.state.list.map(e => {
                pageIdList.push(e.id);
            });
        }

        let columns = [
            {
                Header: (this.state.list &&
                    <DropdownCheck selectionIdList={this.state.selection}
                                   totalIdList={this.state.totalIdList}
                                   pageIdList={pageIdList}
                                   onChange={e => {
                                       this.setState({selection: e});
                                       this.props.updateSelection(e);
                                   }}/>
                ),
                accessor: "id",
                selectType: "checkbox",
                sortable: false,
                Cell: (row, index) => (this.state.list && !(row.statusType && row.statusType === 'TERMINATED') &&
                    <input type="checkbox" checked={this.isSelected(row.id)}
                           onChange={(e) => this.toggleCheckboxChange(e, row, "id", index)}/>),
                width: '3%'
            },
            {
                Header: "Ad",
                accessor: "name",
                Cell: row => (<div style={{margin: 0}}><TextOverFlowTooltip text={row.name} maxLength={12}/></div>)
            },
            {
                Header: "Soyad",
                accessor: "surname",
                Cell: row => (
                    <div style={{margin: 0}}><TextOverFlowTooltip text={row.surname} maxLength={12}/></div>)
            },
            {
                Header: "Departman",
                accessor: "department",
                Cell: row => (
                    <div style={{margin: 0}}>{row.department &&
                        <TextOverFlowTooltip text={row.department.name} maxLength={12}/>}</div>)
            },
            {
                Header: "TCKN",
                accessor: "tckn"
            },
            {
                accessor: [
                    {Header: "e-Posta Adresi", accessor: "email"},
                    {Header: "Telefon Numarası", accessor: "telephone"}
                ],
                Cell: row => (<div>
                    <div style={{margin: 0}}>
                        {row.email}
                    </div>
                    <div style={{margin: 0}}>
                        {(row.telephone ? row.telephone : "")}
                    </div>
                </div>),
                width: "250em"
            },
            {
                Header: "İşe Başlama Tarihi",
                accessor: "employmentStartDate",
                Cell: row => (
                    <div style={{margin: 0}}>{moment(row.employmentStartDate).isValid() &&
                        <TextOverFlowTooltip text={moment(row.employmentStartDate).format("DD-MM-YYYY")}
                                             maxLength={12}/>}</div>),
                width: '9%'
            },
            {
                Header: "Kayıt Durumu",
                accessor: "isRegistered",
                Cell: row => (
                    <div>{!!row.isRegistered ?
                        <label className="fas fa-check"
                               style={{color: '#9fd037', fontSize: '1.2em', cursor: 'pointer'}}/> :
                        ""}
                    </div>),
                sortable: false,
                width: '5%'
            },
            {
                Header: "Durum",
                accessor: "statusType",
                Cell: row => (
                    <div>
                        <span>{statusTypeFilterData().map(function (e) {
                            if (e.value === row.statusType) return e.label
                        })}</span>
                    </div>)
            },
            {
                Header: "Yeni Şifre Talebi Durumu",
                accessor: "linkStatus",
                Cell: row => {
                    console.log(row)
                   return (<div>
                        {((!row.employeeUserId || (!row.isRegistered && !row.userCreated) || (!row.isRegistered && row.userCreated === 0)) && row.linkStatus === "NOT_CREATED") &&
                            <span className="text-info">{"Link Gönderilmesi Bekleniyor"}</span>}
                        {(!!row.employeeUserId && !!row.userCreated && row.userCreated === 1) &&
                            <span className="text-info">{"Link Gönderimi Başlatıldı"}</span>}
                        {/*{(!!row.employeeUserId && !!row.userCreated && (row.userCreated === 0 || row.userCreated === 2)) && row.linkStatus === "NOT_CREATED" &&*/}
                        {/*    <span className="text-info">{"Link Gönderilmesi Bekleniyor"}</span>}*/}
                        {(!!row.employeeUserId && (row.userCreated === 2 || row.userCreated === 0 || row.userCreated === null)) && row.linkStatus === "PASSWORD_SAVED" &&
                            <span className="text-success">{"Şifre Kaydedildi"}</span>}
                        {(!!row.employeeUserId && (row.userCreated === 0 || row.userCreated === 2)) && row.linkStatus === "LINK_TIMEOUT" &&
                            <span className={"text-danger"}>{"Linkin Süresi Doldu"}</span>}
                        {(!!row.employeeUserId && (row.userCreated === 0 || row.userCreated === 2)) && row.linkStatus === "LINK_SENT" &&
                            <span className={"text-warning"}>{"Link Yollandı"}</span>}

                    </div>
                )},
                sortable: false,
                width: "200em"
            },
            {
                Header: "İşlemler",
                accessor: "",
                sortable: false,
                Cell: row => {
                    return (
                        <div style={{textAlign: 'left', WebkitTextFillColor: 'white'}}>
                            <MiniButton title="Detay Görüntüle" faIcon="fa-info"
                                        onClick={() => this.openDetailDialog(row)}
                                        color="info"/>
                            {this.state.list && !(row.statusType && row.statusType === 'TERMINATED') &&
                                <MiniButton title="Şifre Linki Yolla" faIcon="fas fa-key"
                                            disabled={this.state.disableBtn}
                                            onClick={() => {
                                                Swal({
                                                    title: 'UYARI',
                                                    text: ("Şifre değişikliği için link yollanacak. Onaylıyor musunuz?"),
                                                    icon: 'warning',
                                                    showCancelButton: true,
                                                    reverseButtons: true,
                                                    confirmButtonText: 'Tamam',
                                                    confirmButtonColor: '#00ff21',
                                                    cancelButtonText: 'Vazgeç',
                                                    input: 'checkbox',
                                                    inputPlaceholder: 'Var olan şifreyi de sıfırla'
                                                }).then((result) => {
                                                    if (result.value) {
                                                        this.passwordReset(row.employeeUserId)
                                                    } else if (result.value === 0) {
                                                        this.sendLink(row.employeeUserId, 1, 1);
                                                        this.setState({alert: null});
                                                    }
                                                })
                                            }}
                                            color="danger"/>}
                            {this.state.list && !(row.statusType && row.statusType === 'TERMINATED') &&
                                <MiniButton title="KolayPeyk Anasayfa Bilgisi ilet" faIcon="fa-home"
                                            disabled={this.state.disableBtn}
                                            onClick={() => {
                                                this.setState({
                                                    alert: (
                                                        <SweetAlert showCancel warning
                                                                    confirmBtnText="Evet"
                                                                    cancelBtnText="Hayır"
                                                                    confirmBtnBsStyle="success"
                                                                    cancelBtnBsStyle="default"
                                                                    title="Emin misiniz?"
                                                                    onConfirm={() => {
                                                                        if (row.userCreated && row.userCreated === 2) {
                                                                            this.sendHomePageLink(row.employeeUserId, 1, 1)
                                                                        } else {
                                                                            Alert.warning("Lütfen çalışanınza önce kayıt bildirimi yapın.", {
                                                                                position: 'top-right',
                                                                                effect: 'stackslide',
                                                                                timeout: 5000
                                                                            });
                                                                        }
                                                                    }}
                                                                    onCancel={() => this.setState({alert: null})}
                                                        >
                                                            KolayPEYK anasayfa linki gönderilecek.
                                                        </SweetAlert>
                                                    )
                                                });
                                            }}
                                            color="secondary"/>}
                        </div>
                    )
                },
                width: 'auto'
            }
        ];
        return <Table columns={columns}
                      content={this.state.list}
                      loading={this.state.loading}
                      total={this.state.total}
                      page={this.state.page}
                      size={this.state.size}
                      manual={this.state.manual || this.props.manual}
                      options={["5", "10", "20", "50", "100"]}
                      getTrStyles={this.getTrStyles}
                      fetchData={this.fetchData}/>
    }

    render() {
        const fields = [
            {
                label: "İsim",
                accessor: "name",
                type: "text",
                operator: "=",
            },
            {
                label: "Soyisim",
                accessor: "surname",
                type: "text",
                operator: "=",
            },
            {
                label: "Kayıt Durumu",
                accessor: "showOnlyNoFirstAttempt",
                type: "select",
                operator: "=",
                selectData: [
                    {value: ' ', label: 'Seçiniz...'},
                    {value: "true", label: 'Kaydını Tamamlamamışlar'},
                    {value: "false", label: 'Kaydını Tamamlamışlar'}
                ]
            },
            {
                label: "Durum",
                accessor: "statusType",
                type: "select",
                operator: "=",
                selectData: statusTypeFilterDataPassword()
            },
            {
                label: "Yeni Şifre Talebi Durumu",
                accessor: "lastRecord",
                type: "select",
                operator: "=",
                selectData: passwordCreationStatus()
            },
            {
                label: "TCKN",
                accessor: "tckn",
                type: "numericText",
                operator: "=",
            },
            {
                label: "e-Posta Adresi",
                accessor: "email",
                type: "text",
                operator: "=",
            },
            {
                label: "Telefon Numarası",
                accessor: "telephone",
                type: "text",
                operator: "=",
            },
            {
                label: "Departman",
                accessor: "department_name",
                type: "select",
                operator: "=",
                selectData: departmentData(this.state.departmentList),
            },
            {
                label: "Çalışma Şekli",
                accessor: "employmentStatus",
                type: "text",
                operator: "=",
            },
            {
                label: "İşe Başlama Tarihi (Başlangıç)",
                accessor: "min_employmentStartDate", //min_xxx MUST ALWAYS come before the max_xxx, if it exists.
                type: "date",
                operator: ">=",
            },
            {
                label: "İşe Başlama Tarihi (Bitiş)",
                accessor: "max_employmentStartDate",
                type: "date",
                operator: "<=",
            },
        ];

        return <div className="row">
            <div className="card-body">
                <DetailedSearch fields={fields}
                                loading={this.state.searching}
                                onSearch={this.handleSearch}/>
            </div>
            <div className="card-body">
                <div className="row" style={{cursor: 'pointer', marginLeft: '10px', color: 'red'}}>
          <span onClick={this.trigerModal}>
            {(this.state.selection.length !== 0 ? "Seçilen çalışan sayısı " + this.state.selection.length : "")}
          </span>
                </div>
                {this.renderMainTable()}
            </div>
            {this.state.alert}

            {this.state.displayDetailDialog &&
                <PasswordDetails employee={this.state.selected} modal={this.state.displayDetailDialog}
                                 toggle={() => this.setState({displayDetailDialog: false})}
                />}

            {this.state.displaySelectionList &&
                <DisplaySelectionList
                    selection={this.state.selection}
                    modal={this.state.displaySelectionList}
                    closeModal={() => this.setState({displaySelectionList: false})}
                    removeSelected={() => this.setState({selection: [], displaySelectionList: false})}
                />}
        </div>
    }

}

export default NewPasswordRequest;