import React, {Component} from 'react';
import 'react-table/react-table.css';
import {checkGrantToDisplay, create_UUID, showAxiosError} from "../../../helper/helper";
import 'moment/locale/tr';
import {requestK} from "../../../helper/APIUtils";
import {Label, Nav, NavItem, NavLink, TabContent, TabPane} from "reactstrap";
import SweetAlert from "react-bootstrap-sweetalert";
import Alert from "react-s-alert";
import StickButton from "../../../components/StickButton";
import Swal from "sweetalert2";
import NavBarKS from "../../../components/NavBarKS";
import Joyride from "react-joyride";
import classnames from "classnames";
import PasswordBlockedPage from "./component/PasswordBlockedPage";
import NewPasswordRequest from "./component/NewPasswordRequest";
import {EMPLOYEES_PASSWORD_MANAGEMENT} from "../../../common/authorities/GrantDef";
import PasswordBlockedAdminPage from "./component/PasswordBlockedAdminPage";

class PasswordDetailsPage extends Component {

  constructor(props) {
    super(props);
    this.state = {
      alert: null,
      loading: false,
      displayDetailDialog: false,
      manual: false,
      list: [],
      total: 0,
      searching: false,
      searchQuery: undefined,
      page: 0,
      size: 10,
      selected: undefined,
      selectionPassword: [],
      selectionBlocked: [],
      selectedAll: false,
      showOnlyNoFirstAttempt: false,
      steps: [
        {
          target: '.baseBackground',
          content: "Calisan sifre yonetimi",
          placement: "center",
        },
        {
          target: '.zocial',
          content: 'Bordro yüklemek için lütfen "Ücret Bordrosu Yükle" butonuna tıklayınız.',
          placement: "bottom-start",
        },
      ],
      locale: {
        back: 'Geri',
        close: 'Kapat',
        last: 'Son',
        next: 'İleri',
        skip: 'Atla',
      },
      joyrideActive: false,
      shouldRun: true,
      width: window.innerWidth,
      flag: 0,
      showEczaciHelpModal: false,
      video: null,
      stop: false,
      hasAutoRegistry: true,
      activeTab: '0',
      linkList: [],
      updateTable: false
    };
    this.getTrStyles = this.getTrStyles.bind(this);
    this.openDetailDialog = this.openDetailDialog.bind(this);
    this.closeDetailDialog = this.closeDetailDialog.bind(this);
    this.passwordListReset = this.passwordListReset.bind(this);
    this.sendHomePageLink = this.sendHomePageLink.bind(this);
    this.downloadMedia = this.downloadMedia.bind(this);
    this.callback = this.callback.bind(this);
  }

  componentDidMount() {
    this.checkAutoRegistry();
  }

  openDetailDialog(row) {
    let clone = Object.assign({}, row);
    delete clone["_$visited"];
    this.setState({
      selected: clone,
      displayDetailDialog: true
    });
  }

  closeDetailDialog() {
    this.setState({
      selected: undefined,
      displayDetailDialog: false
    });
  }

  getTrStyles(item) {
    return (item.statusType && item.statusType !== 'ACTIVE') ? {
      backgroundColor: 'lightgray',
      webkitTextFillColor: 'darkgray'
    } : {};
  }

  unblockList = () => {
    this.setState({alert: null});
    let self = this;
    if (self.state.selectionBlocked) {
      let params = {
        url: "/passwordCreation/resetListFailLoginAttemptCount",
        method: "post",
        data: self.state.selectionBlocked
      };
      self.setState({disableBtn: true});
      requestK(params)
        .then(function () {
          self.setState({disableBtn: false, selectionBlocked: []});
          return PasswordDetailsPage.alertSuccessReset();
        }).catch(function () {
        self.setState({disableBtn: false});
        return PasswordDetailsPage.alertWarningReset();
      });
    } else {
      Alert.error("Çalışanın kullanıcı hesabı henüz oluşmamıştır. Lütfen daha sonra tekrar deneyiniz.", {
        position: 'top-right',
        effect: 'stackslide',
        timeout: 5000
      });
    }
  };

  //
  // passwordSetReset(userId) {
  //   if (userId) {
  //     let params = {
  //       url: "/passwordCreation/resetPasswordSet",
  //       method: "post",
  //       data: userId
  //     };
  //     requestK(params)
  //       .then(function (response) {
  //       })
  //       .catch(function (error) {
  //       });
  //   }
  // }

  passwordListReset() {
    this.linkListSend("/resetLinkList");
  }

  checkAutoRegistry = () => {
    let self = this;
    let params = {
      url: '/companyinfo/readEmployeeRegistryNotification',
      method: "get"
    };
    requestK(params)
      .then(function (response) {
        self.setState({hasAutoRegistry: response.data});
      }).catch(function (error) {
      showAxiosError(error);
    });
  };

  registryNotification = () => {
    let self = this;
    if (self.state.selectionPassword) {
      let params = {
        url: "/employee/sendRegistryNotifications",
        method: "post",
        data: self.state.selectionPassword
      };
      self.setState({alert: null});
      requestK(params)
        .then(function (response) {
          self.setState({
            alert: (
              <SweetAlert showCancel success
                          confirmBtnText="Evet"
                          cancelBtnText="Hayır"
                          confirmBtnBsStyle="warning"
                          cancelBtnBsStyle="default"
                          title="Kayıt bildirisi gönderim talebi alınmıştır. İşlem sonucu raporda belirtilmektedir."
                          onConfirm={() => self.downloadExcel(response.data)}
                          onCancel={() => self.setState({alert: null, manual: true})}
              >
                <span>Seçili çalışanların raporunu indirmek ister misiniz?</span>
                <br/>
                <img className="simple-text logo-mini" style={{maxWidth: '100px', marginRight: '6px'}} alt={"logo"}
                     src={"data:image/jpeg;base64,"}/>
              </SweetAlert>
            )
          });
        }).catch(function () {
        PasswordDetailsPage.alertWarning(1, 1);
      });
    } else {
      Alert.info("Lütfen çalışan seçiniz.", {
        position: 'top-right',
        effect: 'stackslide',
        timeout: 5000
      });
    }
  };

  downloadExcel = (list) => {
    let self = this;
    list.forEach(function(element,index){
      list[index].employee.organizations = null;
    });
    let params = {
      url: `/employee/downloadExcel`,
      responseType: 'arraybuffer',
      method: "post",
      data: list
    };
    self.setState({alert: null, manual: true});
    requestK(params)
      .then(function (response) {
        let link = document.createElement('a');
        link.href = window.URL.createObjectURL(new Blob([response.data], {type: "application/vnd.ms-excel"}));
        let date = new Date();
        link.download = "Kayıt_Bildirisi_Raporu_" +
          date.getFullYear() + "-" +
          date.getMonth() + "-" +
          date.getDate() + "_" +
          date.getHours() + "-" +
          date.getMinutes() + "-" +
          date.getSeconds() + ".xlsx";
        link.click();
      })
      .catch(function (error) {
        if (error.response === undefined) {
          showAxiosError({message: "Şu an işleminiz gerçekleştirilemiyor."});
        }
        self.setState({alert: null});
      });
  };

  sendLink = (userId, actual, count) => {
    let self = this;
    if (userId) {
      let params = {
        url: "/passwordCreation/managerReset",
        method: "post",
        data: userId
      };
      self.setState({disableBtn: true});
      requestK(params)
        .then(function (response) {
          self.setState({disableBtn: false});
          if (response.data !== "ERROR") {
            self.props.reload(true, "sidebar");
            return PasswordDetailsPage.alertSuccess(actual, count);
          } else {
            return PasswordDetailsPage.alertWarning(actual, count);
          }
        }).catch(function () {
        self.setState({disableBtn: false});
        return PasswordDetailsPage.alertWarning(actual, count);
      });
    } else {
      Alert.error("Çalışanın kullanıcı hesabı henüz oluşmamıştır. Lütfen daha sonra tekrar deneyiniz.", {
        position: 'top-right',
        effect: 'stackslide',
        timeout: 5000
      });
    }
  };

  sendLinkList  = (userList) => {
    let self = this;
    let params = {
      url: "/passwordCreation/resetLinkList",
      method: "post",
      data: userList
    };
    self.setState({disableBtn: true});
    requestK(params)
        .then(function () {
          self.setState({disableBtn: false}, () => self.changeUpdateTable(true));
          return PasswordDetailsPage.alertSuccess(1, 1);
        }).catch(function () {
      self.setState({disableBtn: false});
      return PasswordDetailsPage.alertWarning(1, 1);
    });
  }

  changeUpdateTable = (updateTable) => {
    let self = this;
    self.setState({updateTable});
  }

  sendHomePageLink(userId, actual, count) {
    this.setState({alert: null});
    if (userId) {
      let self = this;
      let params = {
        url: "/passwordCreation/sendHomePageLink",
        method: "post",
        data: userId
      };
      self.setState({disableBtn: true});
      requestK(params)
        .then(function () {
          self.setState({disableBtn: false});
          return PasswordDetailsPage.alertSuccess(actual, count);
        }).catch(function () {
        self.setState({disableBtn: false});
        return PasswordDetailsPage.alertWarning(actual, count);
      });
    } else {
      return PasswordDetailsPage.alertWarning(actual, count);
    }
  }

  homePageListSend() {
    this.linkListSend("/sendHomePageLinkList");
  }

  linkListSend = (url) => {
    this.setState({alert: null});
    let self = this;
    if (self.state.selectionPassword || self.state.selectionBlocked) {
      let params = {
        url: "/employee/getEmployeeUserIdList",
        method: "post",
        data: self.state.linkList
      };
      requestK(params)
        .then(function (response) {
          let userList = response.data;
          let params = {
            url: "/passwordCreation" + url,
            method: "post",
            data: userList
          };
          self.setState({disableBtn: true});
          requestK(params)
            .then(function () {
              self.setState({disableBtn: false}, () => self.changeUpdateTable(true));
              return PasswordDetailsPage.alertSuccess(1, 1);
            }).catch(function () {
            self.setState({disableBtn: false});
            return PasswordDetailsPage.alertWarning(1, 1);
          });
        }).catch(function () {
        PasswordDetailsPage.alertWarning(1, 1);
      });
    } else {
      Alert.info("Lütfen çalışan seçiniz.", {
        position: 'top-right',
        effect: 'stackslide',
        timeout: 5000
      });
    }
  };

  passwordListFullReset = () => {
    this.setState({alert: null});
    let self = this;
    if (self.state.selectionPassword || self.state.selectionBlocked) {
      let params = {
        url: "/employee/getEmployeeUserIdList",
        method: "post",
        data: self.state.linkList
      };
      requestK(params)
          .then(function (response) {
            let userList = response.data;
            let params = {
              url: "/user/resetPasswordList",
              method: "post",
              data: userList
            };
            self.setState({disableBtn: true});
            requestK(params)
                .then(function () {
                  self.passwordSetResetList(userList);
                  self.sendLinkList(userList);
                  self.setState({disableBtn: false});
                  return PasswordDetailsPage.alertSuccess(1, 1);
                }).catch(function () {
              self.setState({disableBtn: false});
              return PasswordDetailsPage.alertWarning(1, 1);
            });
          }).catch(function () {
        PasswordDetailsPage.alertWarning(1, 1);
      });
    } else {
      Alert.info("Lütfen çalışan seçiniz.", {
        position: 'top-right',
        effect: 'stackslide',
        timeout: 5000
      });
    }
  }

  passwordSetResetList = (userList) => {
    if (userList) {
      let params = {
        url: "/passwordCreation/resetPasswordSetList",
        method: "post",
        data: userList
      };
      requestK(params)
          .then(function (response) {
          })
          .catch(function (error) {
          });
    }
  }

  static alertSuccess(actual, count) {
    if (actual >= count) {
      Alert.success("Link başarıyla gönderildi.", {
        position: 'top-right',
        effect: 'stackslide',
        timeout: 5000
      });
    }
  }

  static alertWarning(actual, count) {
    if (actual >= count) {
      Alert.error("Link gönderilemedi, lütfen daha sonra tekrar deneyiniz!", {
        position: 'top-right',
        effect: 'stackslide',
        timeout: 5000
      });
    }
  }

  static alertSuccessReset() {
    Alert.success("Çalışan hesabının blokesi kaldırılmıştır.", {
      position: 'top-right',
      effect: 'stackslide',
      timeout: 5000
    });
  }

  static alertWarningReset() {
    Alert.error("Çalışanın hesabında bloke kaldırılamadı. Lütfen sistem sağlayıcınız ile görüşünüz.", {
      position: 'top-right',
      effect: 'stackslide',
      timeout: 5000
    });
  }

    arrangeDataSet = () => {
        let self = this;
        let dataSet = {};
        dataSet.sheetName = "Çalışan_Şifre_Yönetimi"
        dataSet.uuid = create_UUID();
        dataSet.list = self.state.selectionPassword
        dataSet.type = "excel"
        return dataSet;
    }

    downloadMedia() {
        let self = this;
        let data = self.arrangeDataSet();
        let params = {
            url: `/employee/downloadPasswordReport`,
            method: "post",
            data: data
        };
        requestK(params)
            .then(function () {
                self.setState({loading: false});
                Alert.success("İndirme talebiniz işleme alındı. 'Bekleyen İndirmeler'den' takip edebilirsiniz." , {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 8000
                });
            }).catch(function (error) {
                showAxiosError(error);
        });
    }

  activateJoyride() {
    if (this.state.joyrideActive === false) {
      this.setState({flag: 0});
      this.setState({joyrideActive: true});
      if (this.state.width < 1000) {
        this.setState({flag: 1});
      }
    }
  }

  callback(data) {
    if (data.action === 'close' && data.type === 'step:after') {
      this.setState({shouldRun: false});
      this.setState({flag: 0});
    }
    if (data.action === 'stop' || data.action === 'reset' || data.action === 'skip') {
      this.setState({shouldRun: true});
      this.setState({joyrideActive: false});
      this.setState({flag: 0});
      this.activateJoyride();
    }
  }

  defineTabName = () => {
    if (this.state.activeTab === '2') {
      return 'IK Admin Hesabı Bloke Listesi'
    } else if (this.state.activeTab === '1') {
      return 'Çalışan Bloke Listesi'
    } else {
      return 'Yeni şifre Talebi'
    }
  };

  render() {

    return <div className="row baseBackground">
      {this.state.joyrideActive &&
      <Joyride
        disableBeacon={false}
        autoStart
        // locale={locale}
        // steps={steps}
        continuous={true}
        run={this.state.shouldRun}
        scrollToFirstStep={true}
        showProgress={true}
        showSkipButton={true}
        scrollToSteps={true}
        disableCloseOnEsc={true}
        disableOverlay={false}
        disableOverlayClicks={false}
        keyboardNavigation={true}
        hideBackButton={false}
        disableOverlayClose={true}
        callback={this.callback}
        holePadding={0}
        shouldRun={this.state.shouldRun}
        styles={{
          options: {
            zIndex: 100000,
            primaryColor: "#50a8e2"   // "#fc9636"
          }
        }}/>}
      <Nav className="topbar" style={{paddingLeft: '14px', height: '53px'}}>
        <NavBarKS offsetWidth={this.props.offsetWidth} innerWidth={window.innerWidth}
                  btnDisabled={this.state.btnDisabled}
                  name='archiveInvoiceList'>

          {checkGrantToDisplay(this, EMPLOYEES_PASSWORD_MANAGEMENT) &&
          <StickButton position="leftSide" className="zocial Excel" style={{fontSize: 'x-small'}}
                       label="Excel'e Aktar" title="Seçilen Çalışanları Excel Olarak İndir"
                       disabled={this.state.loading}
                       onClick={() => {
                         if (this.state.selectionPassword && this.state.selectionPassword.length > 0) {
                           this.downloadMedia();
                         } else {
                           this.setState({
                             linkList: this.state.selectionPassword,
                             alert: (
                               <SweetAlert warning
                                           confirmBtnText="Tamam"
                                           confirmBtnBsStyle="warning"
                                           title="Seçim Yapılmadı"
                                           onConfirm={() => this.setState({alert: null})}
                               >
                                 Çalışan seçilmedi.
                               </SweetAlert>
                             )
                           });
                         }
                       }}/>}
          {checkGrantToDisplay(this, EMPLOYEES_PASSWORD_MANAGEMENT) &&
          <StickButton position="leftSide" className="zocial Gonder" style={{fontSize: 'x-small'}}
                       label="Şifre Tekrar Gönder" title="Seçilen Çalışanlara Yeniden Şifre Linki Gönder"
                       disabled={this.state.loading}
                       onClick={() => this.resendPassword()}/>}
          {checkGrantToDisplay(this, EMPLOYEES_PASSWORD_MANAGEMENT) &&
          <StickButton position="leftSide" className="zocial Gonder" style={{fontSize: 'x-small'}}
                       label="Anasayfa Linki Gönder" title="Seçilen Çalışanlara Anasayfa Linki Gönder"
                       disabled={this.state.loading}
                       onClick={() => {
                         if (this.state.selectionPassword && this.state.selectionPassword.length > 0) {
                           this.setState({
                             linkList: this.state.selectionPassword,
                             alert: (
                               <SweetAlert showCancel warning
                                           confirmBtnText="Tamam"
                                           cancelBtnText="Vazgeç"
                                           confirmBtnBsStyle="danger"
                                           cancelBtnBsStyle="default"
                                           title="UYARI"
                                           onConfirm={() => this.homePageListSend()}
                                           onCancel={() => this.setState({alert: null})}
                               >
                                 {'Seçili çalışanlara anasayfa linki yollanacak. Onaylıyor musunuz?'}
                               </SweetAlert>
                             )
                           });
                         } else {
                           this.setState({
                             alert: (
                               <SweetAlert warning
                                           confirmBtnText="Tamam"
                                           confirmBtnBsStyle="warning"
                                           title="Seçim Yapılmadı"
                                           onConfirm={() => this.setState({alert: null})}
                               >
                                 Çalışan seçilmedi.
                               </SweetAlert>
                             )
                           });
                         }
                       }}/>}
          {!this.state.hasAutoRegistry && checkGrantToDisplay(this, EMPLOYEES_PASSWORD_MANAGEMENT) &&
          <StickButton position="leftSide" className="zocial Gonder" style={{fontSize: 'x-small'}}
                       label="Kayıt Bildirisi Gönder" title="Seçilen Çalışanlara İlk kayıt Bildirisi Gönder"
                       disabled={this.state.loading}
                       onClick={() => {
                         if (this.state.selectionPassword && this.state.selectionPassword.length > 0) {
                           this.setState({
                             linkList: this.state.selectionPassword,
                             alert: (
                               <SweetAlert showCancel warning
                                           confirmBtnText="Tamam"
                                           cancelBtnText="Vazgeç"
                                           confirmBtnBsStyle="danger"
                                           cancelBtnBsStyle="default"
                                           title="Onaylıyor musunuz?"
                                           onConfirm={() => this.registryNotification()}
                                           onCancel={() => this.setState({alert: null})}
                               >
                                 {'Kayıt bildirisi almamış çalışanlar için ilk kayıt bildirisi (sms/email) yollanacak.'}
                               </SweetAlert>
                             )
                           });
                         } else {
                           this.setState({
                             alert: (
                               <SweetAlert warning
                                           confirmBtnText="Tamam"
                                           confirmBtnBsStyle="warning"
                                           title="Seçim Yapılmadı"
                                           onConfirm={() => this.setState({alert: null})}
                               >
                                 Çalışan seçilmedi.
                               </SweetAlert>
                             )
                           });
                         }
                       }}/>}
          {this.state.activeTab === '1' &&
          <StickButton position="leftSide" className="zocial Imza" style={{fontSize: 'x-small'}}
                       label="Bloke Kaldır" title="Bloke Kaldır"
                       disabled={this.state.disabledBtn}
                       onClick={() => {
                         if (this.state.selectionBlocked && this.state.selectionBlocked.length > 0) {
                           Swal({
                             title: 'UYARI',
                             text: 'Seçilen çalışanların şifre blokeleri kaldırılacaktır.',
                             icon: 'warning',
                             confirmButtonText: 'Tamam',
                             cancelButtonText: 'Vazgeç',
                             confirmBtnBsStyle: 'danger',
                             cancelBtnBsStyle: 'default',
                             showCancelButton: true,
                             reverseButtons: true,
                             confirmButtonColor: '#00ff21',
                             input: 'checkbox',
                             inputPlaceholder: 'Seçilen çalışanlara şifre değişikliği linki yolla'
                           }).then((result) => {
                             if (result.value) {
                               this.setState({linkList: this.state.selectionBlocked})
                               this.passwordListReset()
                               this.unblockList()
                             } else if (result.value === 0) {
                               this.unblockList()
                               this.setState({alert: null})
                             }
                           })
                         } else {
                           this.setState({
                             alert: (
                               <SweetAlert warning
                                           confirmBtnText="Tamam"
                                           confirmBtnBsStyle="warning"
                                           title="Seçim Yapılmadı"
                                           onConfirm={() => this.setState({alert: null})}
                               >
                                 Çalışan seçilmedi.
                               </SweetAlert>
                             )
                           });
                         }
                       }}/>}
          <StickButton position="leftSide" className="zocial Yenile" style={{fontSize: 'x-small'}}
                       label="Sonuç Yenile" title="Sonuç Yenile"
                       disabled={this.state.disabledBtn}
                       onClick={() => {
                         this.setState({manual: true})
                       }}/>
          {this.state.stop &&
          <div className="helpButtonContainer" position='rightSide'
               style={{marginRight: "4em", marginTop: "1.4em"}}>
            <button className="helpButton" onClick={() => {
              this.activateJoyride()
            }}>
              <span className="tooltipText">Size yardımcı olabilirim</span>
              <i className="far fa-question-circle helpButtonIcon"/>
            </button>
          </div>
          }
        </NavBarKS>
      </Nav>

      <div className="col-sm-12 satirBasi">

        <h4 className="card-title" style={{marginTop: '10px'}}>Çalışan Şifre Yönetimi</h4>
        <div className="card-nav-tabs">

          <div className="col-sm-12">
            <Nav pills className="nav-pills-primary ">
              <NavItem>
                <NavLink
                  className={classnames({active: this.state.activeTab === '0'})}
                  onClick={() => {
                    this.setState({activeTab: '0'})
                  }}>
                  Yeni Şifre Talebi
                </NavLink>
              </NavItem>

              <NavItem>
                <NavLink
                  className={classnames({active: this.state.activeTab === '1'})}
                  onClick={() => {
                    this.setState({activeTab: '1'})
                  }}>
                  Çalışan Bloke Listesi
                </NavLink>
              </NavItem>

              {!!this.state.showIkAdminBlockList &&
              <NavItem>
                <NavLink
                  className={classnames({active: this.state.activeTab === '2'})}
                  onClick={() => {
                    this.setState({activeTab: '2'})
                  }}>
                  IK Admin Hesabı Bloke Listesi
                </NavLink>
              </NavItem>}
            </Nav>
          </div>

          <hr style={{marginBottom: '20px'}}/>

          <div className="card-header card-header-danger">
            <h4 className="card-title">{this.defineTabName()}</h4>
          </div>

          {this.state.activeTab === '0' && <div className="card-header">
            <Label>Çalışanlara (ilk defa/yeniden) şifre oluşturması talebinin gönderileceği ekrandır.
              "Kaydını tamamlamamış çalışanlar" seçimi yapıldığında henüz hiç şifre oluşturumu
              yapmamış çalışanlarınızı listelersiniz. Bu listemeyi yapıp "Linkin Süresi Doldu" durumunda
              olan çalışanlarınıza isteğinize göre yeni şifre talebi gönderebilirsiniz. Yeni Şifre Talebi
              göndermek için ilgili kişinin karşılığındaki kırmızı anahtar tuşuna basınız.
            </Label>
            <Label>Fesih durumundaki çalışanlar burada listelenmemektedir.</Label>
          </div>}

          {this.state.activeTab === '1' && <div className="card-header">
            <Label>Çalışanların şifre blokelerinin kaldırılacağı ekrandır.
              Şifre işlemi bloke edilmiş çalışanlar listelenmiştir. Şifre blokesini kaldırmak
              için ilgili kişinin karşılığındaki tuşuna basınız.
            </Label>
          </div>}

          {this.state.activeTab === '2' && <div className="card-header">
            <Label>IK Admin hesaplarının şifre blokelerinin kaldırılacağı ekrandır.
              Şifre blokesini kaldırmak için ilgili hesabın karşılığındaki tuşuna basınız.
            </Label>
          </div>}

          <div className="card-body">
            <TabContent activeTab={this.state.activeTab}>
              <TabPane tabId="0">
                <div className="">
                  <div className="">
                    <NewPasswordRequest
                      selection={this.state.selectionPassword}
                      updateSelection={updatedList => this.setState({selectionPassword: updatedList})}
                      manual={this.state.manual}
                      updateTable={this.state.updateTable}
                      changeUpdateTable={() => this.changeUpdateTable(false)}
                      refreshCompleted={() => this.setState({manual: false})}/>
                  </div>
                </ div>
              </TabPane>

              <TabPane tabId="1">
                <div className="">
                  <div className="">
                    <PasswordBlockedPage
                      selection={this.state.selectionBlocked}
                      updateSelection={updatedList => this.setState({selectionBlocked: updatedList})}
                      manual={this.state.manual}
                      refreshCompleted={() => this.setState({manual: false})}/>
                  </div>
                </ div>
              </TabPane>

              <TabPane tabId="2">
                <div className="">
                  <div className="">
                    <PasswordBlockedAdminPage
                      selection={this.state.selectionBlocked}
                      updateSelection={updatedList => this.setState({selectionBlocked: updatedList})}
                      manual={this.state.manual}
                      refresh={() => {this.setState({manual: true})}}
                      show={() => this.setState({showIkAdminBlockList: true})}
                      refreshCompleted={() => this.setState({manual: false})}/>
                  </div>
                </ div>
              </TabPane>
            </TabContent>
          </div>

        </div>

      </div>
      {this.state.alert}

    </div>
  }

  resendPassword = () => {
    if (this.state.selectionPassword && this.state.selectionPassword.length > 0) {
      this.setState({
        linkList: this.state.selectionPassword
      });
      Swal({
        title: 'UYARI',
        text: ("Seçili çalışanlara yeni şifre için link yollanacaktır. Onaylıyor musunuz?"),
        icon: 'warning',
        showCancelButton: true,
        reverseButtons: true,
        confirmButtonText: 'Tamam',
        confirmButtonColor: '#00ff21',
        cancelButtonText: 'Vazgeç',
        input: 'checkbox',
        inputPlaceholder: 'Var olan şifrelerini sıfırla'
      }).then((result) => {
        if (result.value) {
          this.passwordListFullReset()
        } else if (result.value === 0) {
          this.passwordListReset();
          this.setState({alert: null});
        }
      })
    } else {
      this.setState({
        alert: (
            <SweetAlert warning
                        confirmBtnText="Tamam"
                        confirmBtnBsStyle="warning"
                        title="Seçim Yapılmadı"
                        onConfirm={() => this.setState({alert: null})}
            >
              Çalışan seçilmedi.
            </SweetAlert>
        )
      });
    }
  }
}

export default PasswordDetailsPage;